      <a-card class="info-card" hoverable>
        <template #title>Адрес</template>
        <p>г. Кызыл, ул. Пушкина, д. 10</p>
      </a-card>

      <a-card class="info-card" hoverable>
        <template #title>Телефон</template>
        <p>+7 (999) 123-45-67</p>
      </a-card>

      <a-card class="info-card" hoverable>
