          <a-form-item>
            <a-button type="primary" html-type="submit">Отправить</a-button>
          </a-form-item>
        </a-form>
      </div>

      <div class="map-wrapper">
        <iframe
          src="https://yandex.ru/map-widget/v1/?um=constructor%3A0a8f758b84d6d01cf8dc0a8cf2cd535a7138dd229716e6c50c6d15a88e5a740d&amp;source=constructor"
          width="100%"
          height="100%"
          frameborder="0"
          allowfullscreen
        ></iframe>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ContactPage',
  data() {
    return {
      form: {
        name: '',
        email: '',
        message: ''
